import { useState } from 'react';
import { useSelector } from 'react-redux';
import { ChatGalleryBlock } from './ChatGalleryBlock';
import { Loader } from './Loader';

export const ChatGallery = ({ totalMediaCount }) => {
    const { senderList, mediaCount } = useSelector(state => state.chat);
    const [isExpanded, setIsExpanded] = useState(false);

    const sortMedia = () => {
        let res = [];
        for (const senderId in senderList) {
            res.push({ id: senderId, ...senderList[senderId] });
        }
        res.sort((a, b) => b.list.length - a.list.length);
        return res;
    }

    const sortedMediaList = sortMedia();
    const percentProgress = mediaCount / totalMediaCount * 100;
    const percentProgressFormatted = (Number.isInteger(percentProgress) ? percentProgress : percentProgress.toFixed(2)) + '%';

    const toggleMembersVisibility = () => {
        setIsExpanded(!isExpanded);
    }

    return (
        <>
        <div className='progress-bar'>
            <div className='progress-bar__line' style={{width: `${mediaCount / totalMediaCount * 100}%`}} />
        </div>
        <h3 className='text-center'>{mediaCount} / {totalMediaCount} ({percentProgressFormatted})</h3>
        <h2 onClick={toggleMembersVisibility} style={{cursor: 'pointer'}}>
            {isExpanded ? 'Hide' : 'Show'} members
        </h2>
        {isExpanded &&
            <nav>
                <ul style={{columnCount: '3'}}>
                    {sortedMediaList.map((sender) => <li key={sender.id}>
                        <a href={`#${sender.id}`}>
                            {sender.name} - {sender.list.length}
                        </a>
                    </li>)}
                </ul>
            </nav>
}
            <div className="d-flex flex-wrap justify-content-center align-items-center">
                {sortedMediaList.map(sender => <ChatGalleryBlock key={sender.id} mediaList={sender.list} id={sender.id}/>)}
                {sortedMediaList.length < 1 && <Loader />}
            </div>
        </>
    );
};
