import React from "react";

export const ChatGalleryBlock = ({ mediaList, id }) => {
    return (
        <div className="border border-dark my-2" id={id}>
            <h3 className="text-center">{mediaList.length}</h3>
            <div className="d-flex flex-wrap justify-content-center align-items-center">
                {mediaList.map(media => {
                    return (
                        <React.Fragment key={media.id}>
                            {media.type === 'messageVideo' ? (
                                <video
                                    src={media.src}
                                    controls={true}
                                    autoPlay={true}
                                    muted={true}
                                    style={{
                                        maxWidth: '100%',
                                        maxHeight: '600px',
                                        height: '100%',
                                        width: media.unsopported && '50px',
                                        height: media.unsopported && '50px',
                                    }}
                                    className="m-2"
                                />
                            ) : (
                                <img
                                    src={media.src}
                                    alt={media.id}
                                    style={{
                                        maxWidth: '100%',
                                        maxHeight: '600px',
                                        height: '100%',
                                        width: media.unsopported && '50px',
                                        height: media.unsopported && '50px',
                                    }}
                                    className="m-2"
                                />
                            )}
                        </React.Fragment>
                    );
                })}
            </div>
        </div>
    );
};
