import './App.css';
import { useEffect, useState } from 'react';
import { QRCodeCanvas } from 'qrcode.react';
import { ChatList } from './components/ChatList';
import { useDispatch, useSelector } from 'react-redux';
import {
    addChat,
    addMedia,
    resetActiveChat,
    setActiveChatIsLoaded,
    sortMedia,
} from './store';
import { Loader } from './components/Loader';
import { ChatGallery } from './components/ChatGallery';
import { Airgram } from '@airgram/web';
import {
    apiHash,
    apiId,
    jsLogVerbosityLevel,
    logVerbosityLevel,
    applicationVersion,
} from './config';

function App() {
    const MAX_MEDIA_COUNT = 2000;
    const [isAuthorized, setIsAuthorized] = useState(false);
    const [qrLoginLink, setQrLoginLink] = useState(null);
    const dispatch = useDispatch();
    const { activeChat, mediaCount } = useSelector(state => state.chat);
    const [airgram, setAirgram] = useState();
    const [isExpanded, setIsExpanded] = useState(false);
    const isLogActive = false;
    const [maxMediaCount, setMaxMediaCount] = useState(MAX_MEDIA_COUNT);

    useEffect(() => {
        const airgram = new Airgram({
            apiId,
            apiHash,
            jsLogVerbosityLevel,
            logVerbosityLevel,
        });
        setAirgram(airgram);

        const requestHandler = req => {
            if (isLogActive) console.log(' [Airgram Request]:', req);
        };

        const updateHandler = update => {
            if (!update) return;
            if (isLogActive) console.log(' [Airgram Update]:', update);
            if (update._ === 'updateAuthorizationState') {
                const { authorizationState } = update;
                switch (authorizationState._) {
                    case 'authorizationStateWaitPhoneNumber':
                        airgram.api.requestQrCodeAuthentication();
                        break;
                    case 'authorizationStateWaitOtherDeviceConfirmation':
                        setQrLoginLink(authorizationState.link);
                        break;
                    case 'authorizationStateReady':
                        setIsAuthorized(true);
                        setQrLoginLink(null);
                        airgram.api.loadChats({ limit: 20 });
                        break;
                    case 'authorizationStateClosed':
                        setIsAuthorized(false);
                        window.location.reload();
                        break;
                    default:
                        return;
                }
            }

            switch (update._) {
                case 'updateNewChat':
                    dispatch(
                        addChat({
                            id: update.chat.id,
                            title: update.chat.title,
                            photoSrc: update.chat.photo
                                ? update.chat.photo.minithumbnail.data
                                : null,
                        })
                    );
                    if (update.chat.id === activeChat?.id)
                        dispatch(setActiveChatIsLoaded());
                    break;
                default:
                    return;
            }
        };

        const responseHandler = (req, res) => {
            if (isLogActive)
                console.log(' [Airgram Response]:', req.method, res);
        };

        airgram.use(async (ctx, next) => {
            if ('request' in ctx) requestHandler(ctx.request);
            else updateHandler(ctx.update);
            await next();
            if ('request' in ctx) responseHandler(ctx.request, ctx.response);
        });
    }, [dispatch, isLogActive]);

    useEffect(() => {
        if (!activeChat || !activeChat.isLoaded || !airgram) return;

        const getMediaList = async count => {
            let fromMessageId = 0;
            for (let i = 0; i < count / Math.min(count, 100); i++) {
                const ctx = await airgram.api.searchChatMessages({
                    chatId: activeChat.id,
                    limit: Math.min(count, 100),
                    filter: { _: 'searchMessagesFilterPhotoAndVideo' },
                    fromMessageId,
                });

                const getFile = async message => {
                    let senderCtx = await airgram.api.getUser({
                        userId: message.senderId?.userId,
                    });
                    senderCtx.response.title =
                        senderCtx.response.firstName +
                        ' ' +
                        senderCtx.response.lastName;
                    if (senderCtx.response.code === 400) {
                        senderCtx = await airgram.api.getChat({
                            chatId: message.senderId?.chatId,
                        });
                    }
                    if (message.content._ === 'messageUnsupported') {
                        dispatch(
                            addMedia({
                                sender: {
                                    id:
                                        message.senderId?.userId ||
                                        message.senderId?.chatId,
                                    name: senderCtx.response.title,
                                },
                                media: {
                                    src: 'https://photos5.appleinsider.com/gallery/26903-39067-000-Mojave-3x2-xl.jpg',
                                    timestamp: message.date,
                                    id: message.id,
                                    unsopported: true,
                                },
                            })
                        );
                        return;
                    }
                    const fileId =
                        message.content._ === 'messageVideo'
                            ? message.content.video.video.id
                            : message.content.photo.sizes.slice(-1).at(0).photo
                                  .id;
                    if (
                        message.content._ === 'messageVideo'
                            ? !message.content.video.video.local.path
                            : !message.content.photo.sizes.slice(-1).at(0).photo
                                  .local.path
                    ) {
                        await airgram.api.downloadFile({
                            fileId,
                            priority: 1,
                            synchronous: true,
                        });
                    }

                    const ctx = await airgram.api.readFilePart({ fileId });
                    dispatch(
                        addMedia({
                            sender: {
                                id:
                                    message.senderId?.userId ||
                                    message.senderId?.chatId,
                                name: senderCtx.response.title,
                            },
                            media: {
                                src: URL.createObjectURL(ctx.response.data),
                                timestamp: message.date,
                                id: message.id,
                                type: message.content._,
                            },
                        })
                    );
                };

                setMaxMediaCount(
                    Math.min(maxMediaCount, ctx.response.totalCount)
                );

                if (
                    !ctx.response.messages ||
                    ctx.response.messages.length < 1
                ) {
                    break;
                }
                for (const message of ctx.response.messages) getFile(message);

                fromMessageId = ctx.response.messages.slice(-1).at(0)?.id;
            }
        };
        getMediaList(MAX_MEDIA_COUNT);
    }, [airgram, dispatch, activeChat]);

    useEffect(() => {
        if (mediaCount >= maxMediaCount) {
            dispatch(sortMedia());
        }
    }, [dispatch, mediaCount]);

    const handleLogoutBtnClick = e => {
        e.preventDefault();
        airgram.api.logOut();
        dispatch(resetActiveChat());
    };

    const handleBackBtnClick = () => {
        setMaxMediaCount(MAX_MEDIA_COUNT);
        dispatch(resetActiveChat());
    };

    const expandClickHandle = () => {
        if (isAuthorized) setIsExpanded(!isExpanded);
    };

    return (
        <>
            <div id="start-anchor"></div>
            <header
                className="position-fixed w-100 bg-white border-bottom p-3"
                style={{ top: '0', left: '0', zIndex: 1 }}
            >
                <div className="container">
                    <h1
                        className="text-center"
                        style={{ cursor: 'pointer' }}
                        onClick={expandClickHandle}
                    >
                        Telegram viewer
                    </h1>
                    {isAuthorized && isExpanded && (
                        <>
                            {activeChat && (
                                <h1 className="text-center">
                                    {activeChat.title}
                                </h1>
                            )}
                            <>
                                <div className="text-center mt-3">
                                    {activeChat && (
                                        <>
                                            <button
                                                className="btn btn-primary me-2"
                                                onClick={handleBackBtnClick}
                                            >
                                                Back
                                            </button>
                                            <a
                                                className="btn btn-primary me-2"
                                                href="#start-anchor"
                                            >
                                                Go up
                                            </a>
                                        </>
                                    )}
                                    <button
                                        className="btn btn-primary"
                                        onClick={handleLogoutBtnClick}
                                    >
                                        Logout
                                    </button>
                                </div>
                            </>
                        </>
                    )}
                </div>
            </header>
            <div
                className="container mt-2"
                style={{ paddingTop: isExpanded ? '200px' : '100px' }}
            >
                {!isAuthorized && (
                    <>
                        {!qrLoginLink && <Loader />}
                        {qrLoginLink && (
                            <div className="text-center mt-4">
                                <QRCodeCanvas
                                    value={qrLoginLink}
                                    size="256"
                                    fgColor="#42AAFF"
                                />
                            </div>
                        )}
                    </>
                )}
                {isAuthorized && (
                    <>
                        {!activeChat && <ChatList />}
                        {activeChat && (
                            <ChatGallery totalMediaCount={maxMediaCount} />
                        )}
                    </>
                )}
            </div>
        </>
    );
}

export default App;
