import { useDispatch } from 'react-redux';
import { setActiveChat } from '../store';

export const Chat = ({ id, title, photoSrc }) => {
    const dispatch = useDispatch();
    const handleChatClick = () => dispatch(setActiveChat({ id, title }));
    return (
        <div className="col">
            <div className="mb-2 chat" onClick={handleChatClick}>
                <div className="avatar me-2">
                    {photoSrc ? (
                        <img
                            src={`data:image/png;base64, ${photoSrc}`}
                            className="w-100 h-100"
                            alt="Avatar"
                        />
                    ) : (
                        title[0]
                    )}
                </div>
                <span className="chat__name">{title}</span>
            </div>
        </div>
    );
};
