import { createSlice, configureStore } from '@reduxjs/toolkit';

const chatSlice = createSlice({
    name: 'chat',
    initialState: {
        list: [],
        activeChat: JSON.parse(localStorage.getItem('activeChat')),
        senderList: {},
        mediaCount: 0,
    },
    reducers: {
        addChat: (state, action) => {
            state.list = [...state.list, action.payload];
        },
        setActiveChat: (state, action) => {
            localStorage.setItem(
                'activeChat',
                JSON.stringify({ ...action.payload, isLoaded: false })
            );
            state.activeChat = action.payload;
            state.activeChat.isLoaded = true;
        },
        setActiveChatIsLoaded: state => {
            state.activeChat.isLoaded = true;
        },
        resetActiveChat: state => {
            localStorage.removeItem('activeChat');
            state.activeChat = null;
            state.senderList = {};
            state.mediaCount = 0;
        },
        addMedia: (state, action) => {
            const newMediaList = [
                ...(state.senderList[action.payload.sender.id]?.list ?? []),
                action.payload.media,
            ];
            state.senderList[action.payload.sender.id] = {
                name: action.payload.sender.name || 'no name',
                list: newMediaList
            };
            state.mediaCount += 1;
        },
        sortMedia: state => {
            for (const senderId in state.senderList) {
                const list = [...state.senderList[senderId].list];
                
                list.sort((a, b) => b.timestamp - a.timestamp);
                state.senderList[senderId].list = list;
            }
        },
    },
});

export const {
    addChat,
    setActiveChat,
    setActiveChatIsLoaded,
    resetActiveChat,
    addMedia,
    sortMedia,
} = chatSlice.actions;

export const store = configureStore({
    reducer: {
        chat: chatSlice.reducer,
    },
});
